.App {
  overflow: hidden;
  margin: 0px;  
  display: flex;  
}

.App > div {
  align-items: stretch;
  height: 100vh;
}

#sidebar {
  flex-shrink: 0;  
  width: 50px;
  width: 100px;
  align-items: stretch;
  /* background-color: #eee; */
  border-right: 1px solid #aaa;
}

#content {
  flex: 1;
  padding-left: 5px;
  background-color: rgb(248,249,250);
}

#site-title {
  margin-top: 10px;
  margin-left: 5px;
}

#recent-trades-table td, tr, table {
  padding: 1px 5px 1px 5px;
  font-size: 12px;
  border-collapse: collapse;
  border: solid black 1px;  
}

#recent-trades-table thead {
  font-weight: bold;
}

h3 {
  margin-bottom: 5px;
}

#homepage-stats-table td {
  border: solid black 1px;
  padding: 1px 3px 1px 3px;
}

#top-markets-table td {
  border: solid black 1px;
  padding: 1px 3px 1px 3px;
}

#top-markets-table thead {
  font-weight: bold;
}